import React from "react";
import { Helmet } from "react-helmet";
import favicon from '../../static/favicon.png'


export default function Layout({ children }) {
  return (
    <>
      <Helmet>
        <link rel="icon" href={favicon} type="image/x-icon" />
        <meta name="description" content="Official portfolio of Anindya Biswas, M.Tech in AI IIT Roorkee , showcasing projects, skills, and achievements in AI, ML, LLM, RAG and software development." />
        <meta name="keywords" content="Anindya Biswas, IIT Roorkee, IITR, MFSDSAI, Mehta Family School of DS and AI, UCSTA, University of Calcutta, Tech Campus, AKCSIT, Amlan Chakrabarti, Durga Toshinwal, Soumya Sen, Wazib Ansar, LLM, RAG, Cohere, OpenAI, portfolio, AI projects, software development, developer" />
        <meta name="author" content="Anindya Biswas" />

        <meta property="og:image" content="https://anindyait.github.io/favicon.png" />
        <meta property="og:url" content="https://anindyait.github.io" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:image" content="https://anindyait.github.io/favicon.png" />

            <script type="application/ld+json">
        {JSON.stringify({
          "@context": "https://schema.org",
          "@type": "Person",
          "name": "Anindya Biswas",
          "url": "https://anindyait.github.io",
          "sameAs": [
            "https://github.com/Anindyait",
            "https://www.linkedin.com/in/anindya-biswas-334795220/"
          ],
          "jobTitle": "AI Developer",
          "worksFor": {
            "@type": "Organization",
            "name": "IIT Roorkee"
          }
        })}
      </script>
      </Helmet>
      {children}
    </>
  );
}
