import React from 'react';
import { motion } from 'framer-motion';

const Transition = ({ onAnimationComplete }) => {
  return (
    <motion.div
    className="fixed top-0 right-0 w-screen h-screen bg-neutral-100 dark:bg-neutral-800 z-50"
    style={{
      clipPath: 'circle(0% at top)', // Start with a tiny circle at the top-right corner
      opacity: 100,
    }}
    animate={{
      clipPath: 'circle(100% at top)', // Expand the hole to cover the screen
      opacity:0,
    }}
    exit={{
      clipPath: 'circle(0% at top)',
      opacity:0, // Shrink the hole back to a point
    }}
    transition={{ duration: 1, ease: 'easeInOut' }}
    onAnimationComplete={onAnimationComplete}>
    
  </motion.div>
  );
};

export default Transition;
