import React from 'react'
import Cookies from 'js-cookie';
import { Link } from "gatsby"
import favicon from '../../static/favicon.png'

import { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMoon, faSun } from '@fortawesome/free-solid-svg-icons';

export default function Header({ children }) {
  const [darkMode, setDarkMode] = useState(false);
  const [showHeader, setShowHeader] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const handleScroll = () => {
        const currentScrollY = window.scrollY;

        if (currentScrollY > lastScrollY && currentScrollY > 50) {
          // User is scrolling down
          setShowHeader(false);
        } else {
          // User is scrolling up
          setShowHeader(true);
        }

        setLastScrollY(currentScrollY);
      };

      window.addEventListener("scroll", handleScroll);

      return () => {
        window.removeEventListener("scroll", handleScroll);
      };
    }
  }, [lastScrollY]);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const savedDarkMode = Cookies.get('darkMode') === 'true';
      setDarkMode(savedDarkMode);

      // Apply the dark mode class to the HTML element
      if (savedDarkMode) {
        document.documentElement.classList.add('dark');
      } else {
        document.documentElement.classList.remove('dark');
      }
    }
  }, []);

  const toggleDarkMode = () => {
    const newDarkMode = !darkMode;
    setDarkMode(newDarkMode);
    Cookies.set('darkMode', newDarkMode, { expires: 365 }); // Save for 1 year

    // Update the `dark` class on the HTML element
    if (newDarkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  };

  return (
    <div id="header" >
      <div  className={`fixed top-2 right-1 text-right py-4 px-3 rounded-2xl  z-50 transition-transform duration-300 backdrop-blur-3xl ${showHeader ? "translate-y-0" : "-translate-y-16"}`}>
        <button onClick={toggleDarkMode} className="me-5 ms-2 text-neutral-700 hover:text-neutral-900 dark:text-neutral-300 dark:hover:text-neutral-100 duration-500">{darkMode ? <FontAwesomeIcon icon={faSun} size='lg' /> : <FontAwesomeIcon icon={faMoon} size="lg"/>}</button>
        <Link to="/works" className="me-4  text-neutral-700 hover:text-neutral-900 dark:text-neutral-300 dark:hover:text-neutral-100 duration-500">Works</Link>
        <Link to="/about" className="me-4  text-neutral-700 hover:text-neutral-900 dark:text-neutral-300 dark:hover:text-neutral-100 duration-500">About</Link>
        <Link to="/" className="me-4  text-red-600 hover:text-red-700 dark:hover:text-red-500 duration-500">Home</Link>
      </div>
      {/* Content */}
      {children}
    </div>
  );
}
